import { v4 as uuid } from 'uuid';

/**
 * 
 * 
 * @returns {String}
 */
const getUUID = function () {
    return uuid().replaceAll('-', '')
}

/**
 * 
 * 
 * @param {Date} date1 
 * @param {Date} date2 
 * @returns {String}
 */
const getNumberOfDays = function (date1, date2 = new Date()) {
    //
    //date1date2
    let a1 = Date.parse(new Date(date1));
    let a2 = Date.parse(new Date(date2));
    let day = parseInt((a2 - a1) / (1000 * 60 * 60 * 24)); //
    if (day > 356) return (day % 356) + ''
    else if (day > 30) return (day % 12) + ''
    else if (day > 7) return (day % 7) + ''
    else if (day > 1) return day + ''
    else {
        let hour = parseInt((a2 - a1) / (1000 * 60 * 60))
        if (hour >= 1) return hour + ''
        else {
            let minite = parseInt((a2 - a1) / (1000 * 60))
            if (minite >= 1) return minite + ''
            else return ''
        }
    }
}

/**
 * 
 * @param {Date} date 
 * @returns 
 */
const getDate = function(date) {
    let time = new Date(date)
    console.log(time);
    return time.getMonth() + '-' + time.getDay()
}

/**
 * 
 * @param {} fun 
 * @param {*} delay 
 * @returns 
 */
 function throttle(fun, delay) {
    let last, deferTimer
    return function (args) {
        let that = this;
        let _args = arguments;

        let now = +new Date();
        if (last && now < last + delay) {
            clearTimeout(deferTimer);
            deferTimer = setTimeout(function () {
                last = now;
                fun.apply(that, _args);
            }, delay)
        } else {
            last = now;
            fun.apply(that, _args);
        }
    }
}

export {
    getUUID, getNumberOfDays, getDate, throttle
}
